@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Prata&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fira+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alegreya+Sans+SC:ital,wght@1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alegreya+Sans+SC:wght@100;300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Body {
  display: block;
  text-align: start;
  font-family: 'Open Sans', sans-serif;
  padding-left: 25%;
  padding-right: 25%;
  padding-bottom: 5%;
  max-width: 900px;
  min-width: 400px;
}

.Footer {
  margin-top: 2%;
  padding-left: 25%;
  padding-right: 25%;
  padding-bottom: 5%;
}

.test div {
  background: white;
  border: 1px solid black;
  width: 75px;
  height: 20px;
  display: inline-block;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  color: black;
  font-family: 'Prata', serif;
  -webkit-margin-after: 1%;
          margin-block-end: 1%;
}

.App-link {
  color: #61dafb;
}
